.background-one {
    position: relative;
    min-height: 100vh;
    background-image: url('/background.webp');
    background-size: cover; /* Make sure the background image covers the whole container */
    background-position: center; /* Optionally center the background image */
  }
  
  .background-one::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.5); /* Overlay color (black with 50% opacity) */
    z-index: 1; /* Ensures the overlay is on top of the background but below the content */
  }

  

  .login-page .login-sec{
    max-width: 900px;
    padding: 10px;
    background-color: var(--);
  }